export default{
	data(){
		return{
		catelist:[]
	}
},
mounted(){
	uni.setNavigationBarTitle({
		title:'分类'
	}),
	this.getlist()
},
methods: {
	getlist() {
		this.request({
			url:'http://157.122.54.189:9088/image/v1/vertical/category'
		}).then(res=>{
			this.catelist = res.res.category;
			console.log(this.catelist);
		})
		
	}
},
}
